/* SOCIAL STUFF */
.social-container {
  background: #eeeeee;
  padding: 25px 50px;
  background-color: grey;
}
a.social {
  margin: 0 1rem;
  transition: transform 250ms;
  display: inline-block;
}

a.social:hover {
  transform: translateY(-2px);
}

a.linkedin {
  color: #0077b5;
}

a.facebook {
  color: #4968ad;
}

a.twitter {
  color: #49a1eb;
}

a.instagram {
  color: black;
}

a.youtube {
  color: #FF0000;
}